"use strict";
import React from "react";
import {Link} from "react-router-dom";

export default function AppNavbar(p){return (
    <div className="AppNavbar" {...p} style={{marginTop:"5vh"}}>
        {/* <DivLink to="/"></DivLink> */}
        {/* <DivLink to="/Signup"></DivLink>
        <DivLink to="/Login"></DivLink> */}
        <DivLink to="/ExhibitCreator"> </DivLink>
        <DivLink to="/ExhibitOverview"> </DivLink>
        <DivLink to="/ExhibitMockupOverview"></DivLink>
    </div>
);};

function DivLink(p){
    return (
        <Link className="DivLink" to={p.to}>
            <div>{p.children}</div>
        </Link>
    );
}


